import React, { useState } from "react";
import UserTable from "./tables/UserTable";
import AddUserForm from "./forms/AddUserForm";
import EditUserForm from "./forms/EditUserForm";

const App = () => {
    const usersData = [
        { id: 1, name: "Liza", username: "floppydiskette" },
        { id: 2, name: "Craig", username: "idol5pace" },
        { id: 3, name: "Barkley", username: "chuckwagon39" },
        { id: 4, name: "Bonita", username: "ereS.miO.nuncA" }
    ];

    const initialFormState = {
        id: null,
        name: "",
        username: ""
    };

    const [users, setUsers] = useState(usersData);
    const [editing, setEditing] = useState(false);
    const [currentUser, setCurrentUser] = useState(initialFormState);

    const addUser = user => {
        user.id = users.length + 1;
        setUsers([...users, user]);
    };

    const updateUser = (id, updatedUser) => {
        setEditing(false);

        setUsers(users.map(user => (user.id === id ? updatedUser : user)));
    };

    const deleteUser = id => {
        setEditing(false);

        setUsers(users.filter(user => user.id !== id));
    };

    const editRow = user => {
        setEditing(true);

        setCurrentUser({
            id: user.id,
            name: user.name,
            username: user.username
        });
    };

    return (
        <div className="container">
            <h1>Hoox User Management</h1>
            <h4>CRUD app built with React and Hooks</h4>
            <div className="flex-row">
                <div className="flex-large">
                    {editing ? (
                        <div>
                            <h2>Edit user</h2>
                            <EditUserForm
                                editing={editing}
                                setEditing={setEditing}
                                currentUser={currentUser}
                                updateUser={updateUser}
                            />
                        </div>
                    ) : (
                        <div>
                            <h2>Add user</h2>
                            <AddUserForm addUser={addUser} />
                        </div>
                    )}
                </div>
                <div className="flex-large">
                    <h2>View users</h2>
                    <UserTable
                        users={users}
                        editRow={editRow}
                        deleteUser={deleteUser}
                    />
                </div>
            </div>
        </div>
    );
};

export default App;
